<template>
  <el-container class="container">
    <el-header style='display: flex;align-items: center;justify-content: center;'>
      <div>
        <span></span>
        <span v-for="(item, index) in list"
              :key="index"
              class="item"
              @click="queryList(index)">{{ item }}</span>
      </div>
    </el-header>
    <el-container style="height:calc(100% - 60px)">
      <el-aside width="200px">
        <div>
          <div></div>
          <!-- <el-input placeholder=""
                    v-model="filterText">
          </el-input> -->
          <el-tree :data="nodeData"
                   node-key="id"
                   lazy
                   @node-click='check'
                   ref="tree"
                   :load="loadNode"
                   :filter-node-method="filterNode">
            <span class="custom-tree-node"
                  slot-scope="{ node, data }"
                  node-click='check'>
              <span><i class="el-icon-folder-opened"
                   v-if="data.isDirectory"></i>
                <i v-else
                   class="el-icon-tickets"></i> {{ node.label }}</span>
            </span>
          </el-tree>
        </div>
      </el-aside>
      <el-container>
        <el-main>
          <el-row :gutter="10">
            <el-form ref="form"
                     :model="form"
                     label-width="80px">
              <el-col :span="6">
                <el-form-item label="">
                  <el-select v-model="templateValue"
                             placeholder="">
                    <el-option v-for="item in templateOptions"
                               :key="item.value"
                               :label="item.label"
                               :value="item.path">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="">
                  <el-select v-model="dateValue"
                             placeholder="">
                    <el-option v-for="item in dateOptions"
                               :key="item.value"
                               :label="item.label"
                               :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="">
                  <el-input v-model="url"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-row type='flex'
                        :gutter="10">
                  <el-button type="primary"
                             @click="showSwaggerList">
                    
                  </el-button>
                  <!-- <el-button type="primary"
                            @click="getSwagger"
                            v-if="dateValue != 'sw'">
                    
                  </el-button> -->
                  <el-upload style='margin-left:10px'
                             class="upload-demo"
                             action="/api/upload"
                             :show-file-list="false"
                             :on-success="handleAvatarSuccess"
                             multiple
                             :limit="3"
                             accept=".js,.ts,.java,.go,.py"
                             :file-list="fileList">
                    <el-button type="primary"></el-button>
                  </el-upload>
                  <el-button type="primary"
                             style='margin-left:10px'
                             @click="downLoadTemplate">
                    
                  </el-button>
                </el-row>
              </el-col>
            </el-form>
          </el-row>
          <el-row>
            <h5>{{path}}</h5>
            <h5></h5>
            <textarea v-model="templateSting"
                      style="height:500px;width:100%;background-color: #f3f3f3;font-size:12px;overflow: visible;font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,monospace;font-size: 12px;color: #24292e; word-wrap: normal;white-space: pre;padding:20px"></textarea>
          </el-row>
        </el-main>
        <el-footer>Footer</el-footer>
      </el-container>
    </el-container>
    <el-dialog title=""
               :visible.sync="dialogVisible"
               width="80%">
      <!-- <el-transfer v-model="transferValue"
                   :data="transferData"></el-transfer> -->
      <el-table ref="multipleTable"
                :data="transferData.filter(data => !search || data.key.toLowerCase().includes(search.toLowerCase()))"
                tooltip-effect="dark"
                style="width: 100%"
                height="450"
                @filter-change='handFilter'
                @selection-change="handleSelectionChange">
        <el-table-column type="selection"
                         width="55">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{ scope.row.key }}</template>
        </el-table-column>
        <el-table-column label=""
                         prop="type">
        </el-table-column>
        <el-table-column>
          <template slot="header">
            <el-input v-model="search"
                      size="mini"
                      placeholder="" />
          </template>
          <template slot-scope="scope">
            {{scope.row.data.summary}}
          </template>
        </el-table-column>
        <!-- <el-table-column prop="address"
                         label=""
                         show-overflow-tooltip> --> -->
        <!-- </el-table-column> -->
      </el-table>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="takeAction"> </el-button>
      </span>
    </el-dialog>
  </el-container>
  <!-- <div>
      <div></div>
      <el-upload class="upload-demo"
                 action="/api/upload"
                 multiple
                 :limit="3"
                 :on-exceed="handleExceed"
                 :file-list="fileList">
        <el-button size="small"
                   type="primary"></el-button>
      </el-upload>
    </div> -->
</template>

<script>
// jsjsjson
// import  from '';
import {
  getCatalogue,
  getCatalogueList,
  getSwaggerAction,
  generateTs,
  getTemplate,
  downLoad
} from '../../api/api'
export default {
  // import
  components: {},
  data () {
    // 
    return {
      fileList: [],
      selectList: [],
      dialogVisible: false,
      transferValue: [],
      transferData: [],
      filterText: '',
      catalogue: {
        path: ''
      },
      path: '',
      form: {},
      search: '',
      templateValue: '',
      dateValue: 'sw',
      templateSting: '',
      templateOptions: [],
      // templateOptions: [
      //   {
      //     value: 'ts',
      //     label: 'TypeScript'
      //   },
      //   {
      //     value: 'java',
      //     label: 'Java'
      //   },
      //   {
      //     value: 'go',
      //     label: 'Go'
      //   },
      //   {
      //     value: 'python',
      //     label: 'Python'
      //   },
      //   {
      //     value: 'self',
      //     label: ''
      //   }
      // ],
      dateOptions: [
        {
          value: 'sw',
          label: 'Swagger'
        },
        {
          value: 'res',
          label: 'RestfulApi'
        },
        {
          value: 'Mysql',
          label: 'Mysql'
        }
      ],
      nodeData: [],
      swagger: '',
      url: '',
      flag: true
    }
  },
  //  data
  computed: {
    list: function () {
      // `this`  vm 
      return this.catalogue.path.split('\\')
    }
  },
  // data
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val)
    },
    search () {
      this.flag = false
      // this.$refs.multipleTable.toggleRowSelection(this.selectList)
    }
  },
  // 
  methods: {
    handleAvatarSuccess () {
      this.$message({
        message: '',
        type: 'success'
      })
      this.getTemplateList()
      return null
    },
    handleSelectionChange (value) {
      if (this.flag) {
        this.selectList = value
      } else {
        this.flag = true
        this.selectList.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      }
    },
    handFilter (f) {
      console.log(f)
    },
    takeAction () {
      if (this.selectList.length === 0) {
        this.$message({
          message: 'api',
          type: 'warning'
        })
        return
      }
      var path = this.$refs.tree.getCurrentNode()
        ? this.$refs.tree.getCurrentNode().path
        : this.path
      generateTs({ swagger: this.selectList, path: path, templateUrl: this.templateValue }).then(resThen => {
        this.$message({
          message: '',
          type: 'success'
        })
        this.templateSting = resThen
        this.dialogVisible = false
      })
    },
    /**
     * @author: 
     * @name: showSwaggerList
     * @description: swagger
     * @param {type}: 
     * @return {type}: 
     * @example: 
     */
    showSwaggerList () {
      if (!this.url) {
        this.$message({
          type: 'warning',
          message: 'url'
        })
        return null
      }
      this.dialogVisible = true
      this.transferData = []
      getSwaggerAction({ url: this.url }).then(res => {
        // this.transferData = res.paths
        let paths = res.paths

        for (let k in paths) {
          let child = paths[k]
          let childData = {}
          let type = ''
          if (child.hasOwnProperty('post')) {
            childData = child['post']
            type = 'POST'
          } else if (child.hasOwnProperty('get')) {
            childData = child['get']
            type = 'GET'
          }
          let item = {
            key: k,
            data: childData,
            type: type
          }
          this.transferData.push(item)
        }
      })
    },
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    getCatalogueAction () {
      getCatalogue().then(res => {
        this.catalogue = res
        this.path = res.path
        getCatalogueList({ path: res.path }).then(back => {
          this.nodeData = back
          console.log(back)
        })
      })
    },
    /**
     * @author: 
     * @name: getTemplateList
     * @description: 
     * @param {type}: 
     * @return {type}: 
     * @example: 
     */
    getTemplateList () {
      getTemplate().then(res => {
        this.templateOptions = res
        this.templateValue = res[0]['path']
      })
    },
    /**
     * @author: 
     * @name: downLoadTemplate
     * @description: 
     * @param {type}: 
     * @return {type}: 
     * @example: 
     */
    downLoadTemplate () {
      downLoad({ path: this.templateValue }).then(res => {
        let url = window.URL.createObjectURL(new Blob([res]))
        let name = this.templateValue.split('\\').pop()
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', name)
        document.body.appendChild(link)
        link.click()
      })
    },
    queryList (index) {
      let array = []
      this.list.forEach((element, i) => {
        if (i <= index) {
          array.push(element)
        }
      })
      console.log(array)
      this.path = array.join('\\')
      getCatalogueList({ path: array.join('\\') }).then(res => {
        this.nodeData = res
        console.log(res)
      })
    },
    loadNode (node, resolve) {
      if (node.level === 0) {
        resolve([])
        return
      }
      getCatalogueList({ path: node.data.path }).then(res => {
        resolve(res)
      })
    },
    check (data) {
      this.path = data.path
    },
    getSwagger () {
      var path = this.$refs.tree.getCurrentNode()
        ? this.$refs.tree.getCurrentNode().path
        : this.path
      getSwaggerAction().then(res => {
        generateTs({ swagger: res, path: path }).then(resThen => {
          this.$message({
            message: '',
            type: 'success'
          })
          this.templateSting = resThen
        })
      })
    }
  },
  //  - this
  created () {
    this.getCatalogueAction()
    this.getTemplateList()
  },
  //  - DOM
  mounted () { },
  beforeCreate () { }, //  - 
  beforeMount () { }, //  - 
  beforeUpdate () { }, //  - 
  updated () { }, //  - 
  beforeDestroy () { }, //  - 
  destroyed () { }, //  - 
  activated () { } // keep-alive
}
</script>
<style lang="scss" scoped>
.container {
  height: 100%;
}
.item {
  border: 1px solid green;
}
</style>
